import React, { Component } from "react";
import SFCMain from "../Home/images/SFC/1.png";
import { withTranslation } from "react-i18next";

export class SFC extends Component {
  render() {
    const { t } = this.props;
    return (
      <main>
        <div className="container container-wrapper">
          <div className="row">
            <div className="col-lg-7 col-md-6 mb-4">
              <h2>{t("SFC:SFC1")}</h2>
              <p>{t("SFC:SFC2")}</p>
              <div className="registerFair">
                <a
                  href="https://app.nivesh.com/home/index"
                  target="_blank"
                  title=""
                  className="btn btn-primary"
                >
                  {t('CTAFooter:InvestNow1')}
                </a>
              </div>
            </div>
            <div className="col-lg-5 col-md-6 mb-4">
              <img src={SFCMain} className="img-fluid" />
            </div>
          </div>
        </div>

        <div className="gardient-red">
          <div className="container container-wrapper">
            <div className="row">
              <h2 className="text-white">{t("SFC:SFC3")}</h2>
              <p className="text-white">{t("SFC:SFC4")}</p>
            </div>
          </div>
        </div>

        <div className="container container-wrapper">
          <div className="row">
            <div className="col-lg-12">
              <h2>{t("SFC:SFC5")}</h2>
              <p>{t("SFC:SFC6")}</p>
            </div>
            <div className="col-lg-12">
              <h2>{t("SFC:SFC7")}</h2>
              <p>{t("SFC:SFC8")}</p>
              <p> {t("SFC:SFC9")}</p>
              <p> {t("SFC:SFC10")}</p>
              <p>{t("SFC:SFC11")}</p>
              <p> {t("SFC:SFC12")}</p>
            </div>
            <div className="clearDiv"></div>
          </div>
          <div className="text-center">
            <a
              href="https://app.nivesh.com/home/index"
              target="_blank"
              title=""
              className="btn btn-primary"
            >
              {t('CTAFooter:InvestNow1')}
            </a>
          </div>
        </div>
      </main>
    );
  }
}

export default withTranslation()(SFC);
